import { Box } from '@mui/material';
import Post from './Post';

// Images
import man1 from './../assets/man1.jpg';
import man2 from './../assets/man2.webp';
import man3 from './../assets/man3.jpg';
import woman1 from './../assets/woman1.avif';
import woman2 from './../assets/woman2.jpg';
import woman3 from './../assets/woman3.webp';
import pasta from './../assets/pasta.jpg';
import soup from './../assets/soup.webp';
import meatball from './../assets/meatball.webp';
import muffins from './../assets/muffins.webp';
import dough from './../assets/dough.webp';
import bisquits from './../assets/bisquits.webp';

const infos = [
  {
    title: 'John Smith',
    date: 'November 15, 2023',
    src: man1,
    text: "The person who recipe-tested this copycat Olive Garden Fettuccine Alfredo said, 'Holy smokes this is good! It's LEGIT Alfredo sauce.' That's exactly what we aimed for when we set out to develop one of Olive Garden's most beloved dishes. The sauce is creamy, cheesy, and garlicky, and clings to the pasta effortlessly thanks to the easy bchamel sauce. This pasta comes together in 20 minutes, which is likely faster than it takes most of us to drive to the closest Olive Garden.",
    image: pasta,
  },
  {
    title: 'Michael Johnson',
    date: 'November 11, 2023',
    src: man2,
    text: 'What if I told you theres a way to make delicious soup at home that wont require a ton of work? Its time to preheat your oven and get that big soup pot out because Leslie Bobo-Kiesel, the cook behind @gathering.page, has a viral butternut squash tomato soup recipe with more than 3 million views that proves you can and should make soup (at least occasionally) from scratch. The best part? You can use her brilliant trick to make so many different kinds of soups beyond this recipe.',
    image: soup,
  },
  {
    title: 'Robert Davis',
    date: 'November 10, 2023',
    src: man3,
    text: "If you, like me, struggle with runaway meatballs whenever you try to chow down on a juicy sub sandwich full of meatballs (no matter how tightly you white knuckle it), this soups for you. It's a meatball sub in all its glory, but deconstructed into a soup. You get all of the flavors and all of the cheese without any of the fuss.",
    image: meatball,
  },
  {
    title: 'Jennifer Taylor',
    date: 'November 9, 2023',
    src: woman1,
    text: 'Its hard to resist a tall muffin with a domed top. Thats why coffee shops like Starbucks stuff their cases with all kinds of muffins, ideal for eating on the go and pairing with a cup of coffee. Save yourself more than a few bucks and make bakery-style muffins at home. Its easy! These double chocolate muffins are tall, moist, and come together in a single bowl (no mixer required) in no time.',
    image: muffins,
  },
  {
    title: 'Emily Anderson',
    date: 'November 8, 2023',
    src: woman2,
    text: "Maybe you passed by the fresh balls of pizza dough in the grocery store and grabbed one on a whim and now you need inspiration. Maybe you made a double batch of dough a while back and have half of it in your freezer. Or maybe you just feel like being creative today. Having a ball of pizza dough to use doesn't mean you have to make a classic pizza. With these 12 recipes, you can get creativefrom breakfast pizza to calzones to sweet monkey bread. Or go pizza-ish with calzone, stromboli, or pepperoni rolls.",
    image: dough,
  },
  {
    title: 'Sarah Clark',
    date: 'November 8, 2023',
    src: woman3,
    text: 'Biscuits can be a little bit intimidating. There are lots of recipes out there and many of them require extensive lamination and chilling. If all of this complicated terminology makes you want to abandon making biscuits altogether, then its time to make drop biscuits. Drop biscuits come together in one bowl and are quick, easy, and foolproof. Theyre called drop biscuits because you simply scoop and drop the biscuit batter onto the parchment paper and bake! The biscuits are crispy and golden brown on the outside with a soft interior.',
    image: bisquits,
  },
];

const Feed = () => {
  return (
    <Box flex={4} p={2}>
      {infos.map((el, index) => (
        <Post
          key={index}
          title={el.title}
          date={el.date}
          src={el.src}
          text={el.text}
          image={el.image}
        />
      ))}
    </Box>
  );
};

export default Feed;
